<template>
  <div>
    <div style="border-bottom: 3px solid black; height: 10px; margin-top: 5%">
      <br />
      <button
        style="
          font-size: 30px;
          font-weight: bold;
          border: none;
          background: white;
          text-align: center;
          float: right;
        "
        @click="[createNewForm(), getColor()]"
      >
        +
      </button>
    </div>

    <div
      class="formOuterBox"
      :class="{ isFormOpen: formBox }"
      :style="{ background: color }"
      v-if="isFormOpen"
    >
      <form>
        <a>{{ moodTable[this.contents.mood] }}</a>
        <button class="submitButton" @click="saveContents" type="submit">
          작성
        </button>
        <textarea
          style="
            width: 99%;
            height: 200px;
            display: block;
            font-size: 30px;
            font-weight: bold;

            background: none;
            border: none;
            outline: none;
            text-align: left;
            overflow: hidden;
          "
          v-model="context"
        ></textarea>
      </form>
    </div>
    <div
      v-for="(row, idx) in this.savedContents"
      :key="(idx, componentKey)"
      class="box"
      :style="{ background: this.savedContents[idx].color }"
    >
      <a
        style="font-size: 30px; float: left; margin-left: 2%; margin-top: 10px"
      >
        {{ this.moodTable[this.savedContents[idx].mood] }}</a
      >
      <a class="context"> {{ this.savedContents[idx].date }}</a>
      <br />
      <a class="context">{{ this.savedContents[idx].context }} </a>
    </div>
  </div>
</template>

<script>
import router from "@/router/router";
export default {
  name: "contentBox",
  data() {
    return {
      color: 0,
      isFormOpen: false,
      contents: {
        context: "",
        color: "",
        mood: this.$store.state.todaysMood.contents.mood,
        date: "",
      },
      moodTable: {
        1: "😐",
        2: "😁",
        3: "😂",
        4: "😔",
        5: "😨",
        6: "😡",
      },
      context: "",
      savedContents: [],
      componentKey: 0,
    };
  },

  components: {},
  mounted() {
    this.loadContents();
    console.log(this.$store.state.todaysMood.contents.date);
  },

  methods: {
    getColor() {
      const colors = `hsl(${parseInt(Math.random() * 24, 10) * 15}, 50%, 57%)`;
      this.color = colors;
    },
    createNewForm() {
      this.context = "";
      this.isFormOpen = true;
    },

    saveContents() {
      this.contents.color = this.color;
      this.contents.context = this.context;
      this.contents.mood = this.$store.state.todaysMood.contents.mood;
      this.contents.date = this.$store.state.todaysMood.contents.date;
      this.isFormOpen = false;

      this.$axios
        .post("/todaysMood/save", this.contents)
        .then((res) => {
          alert(res.data);
          router.go();
        })
        .catch(function (error) {
          alert(error);
        });
    },
    loadContents() {
      this.$axios
        .get("/todaysMood/list")
        .then((res) => {
          this.savedContents = res.data;
        })
        .catch(function (error) {
          alert(error);
        });
    },
    init() {
      this.$store.commit("initToDoList");
    },
  },
};
</script>

