}
.formOuterBox {
  animation-duration: 1s;
  animation-name: open;
  border-radius: 12px;
  width: 80%;
  height: 200px;
  background: gray;
  display: inline-block;
  margin-top: 10px;
  overflow: auto;
  text-align: center;
  margin-left: 10vw;
}

